import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { ColEditDelete } from 'components/common';
import { Card, Row, message, Table, Col } from 'antd';
import { getListFaculty, deleteFaculty } from 'services/Facultys';

import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA = routeConstant.DANH_MUC__DAO_TAO__KHOA_DAO_TAO__NHAP_DU_LIEU;
class ListDepartmentTrainingPage extends Component {
  state = {
    departmentTraining: [],
    loading: false,
    pagination: {},
  };
  componentDidMount() {
    this.getListData();
  }

  getListData = page => {
    this.setState({
      loading: true,
    });
    getListFaculty(page)
      .then(res => {
        const pagination = { ...this.state.pagination };
        pagination.total = res.data.totalResults;
        this.setState({
          departmentTraining: res.data.items,
          loading: false,
          pagination,
        });
      })
      .catch(error => {
        console.log('====>', error);
        this.setState({
          loading: false,
        });
      });
  };

  handleTableChange = pagination => {
    const pager = { ...this.state.pagination };
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getListData(pagination.current);
  };

  confirmDelete = e => {
    this.setState({
      loading: true,
    });
    deleteFaculty(e)
      .then(() => {
        message.success('Xóa khoa đào tạo thành công');
        this.getListData();
        this.setState({
          loading: false,
        });
      })
      .catch(error => {
        this.setState({
          loading: false,
        });
        console.log('res', error);
        // message.error(error.response.data.reason);
      });
  };

  render() {
    const { departmentTraining, pagination, loading } = this.state;
    const columns = [
      {
        title: 'Mã khoa',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên khoa',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.confirmDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Khoa đào tạo">
        <Row>
          <Col span={24}>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              bordered
              columns={columns}
              dataSource={departmentTraining}
              pagination={pagination}
              onChange={this.handleTableChange}
              loading={loading}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListDepartmentTrainingPage />
  </Page>
);



